import React from "react";
import Card from "@material-ui/core/Card";
import CardMedia from "@material-ui/core/CardMedia";
import CardActionArea from "@material-ui/core/CardActionArea";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/styles";
import CardContent from "@material-ui/core/CardContent";
import ButtonBase from "@material-ui/core/ButtonBase";

const cardHeight = 250;
const cardWidth = (2 / 3) * cardHeight;
const _ = require("lodash");

const useStyles = makeStyles(theme => ({
  card: {
    flexDirection: "column",
    width: cardWidth
  },
  media: { height: cardHeight, width: cardWidth, backgroundColor: "#212121" },
  cardContent: {
    backgroundColor: "#000000",
    marginTop: theme.spacing(1.3725)
  },
  cardText: {
    color: "white", // color: "#e0e0e0",
    fontFamily: "Roboto", // fontFamily: "Segoe UI",
    fontSize: 15,
    fontWeight: 400,
    lineHeight: 1.4
  },
  cardSubtext: {
    color: "#9e9e9e", //  color: "#bbdefb",
    fontFamily: "Roboto", // fontFamily: "Segoe UI",
    fontSize: 13, // fontSize: 13,
    fontWeight: 400 //
  },
  cardAction: {
    display: "block",
    textAlign: "initial"
  },
  actionArea: {
    "&:hover $focusHighlight": {
      opacity: 0.38
    }
  },
  focusHighlight: {}
}));

export default function MediaCard({ media_, openDialogue }) {
  const { title, genres, posterLink, backdropLink } = media_;
  const classes = useStyles();
  return (
    <div>
      <Card className={classes.card} style={{ borderRadius: 0 }}>
        <CardActionArea
          classes={{
            root: classes.actionArea,
            focusHighlight: classes.focusHighlight
          }}
          onClick={() => openDialogue(media_)}
          onTouchTap={e => {
            e.preventDefault();
            openDialogue();
          }}
        >
          <CardMedia image={posterLink} className={classes.media} />
        </CardActionArea>
      </Card>
      <div className={classes.cardContent}>
        <Typography className={classes.cardText}>{title}</Typography>
        <Typography className={classes.cardSubtext} style={{ marginTop: -0.5 }}>
          {genres.slice(0, 2).join(", ")}
        </Typography>
      </div>
    </div>
  );
}
