import React from "react";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Box from "@material-ui/core/Box";
import { makeStyles, fade } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import SearchIcon from "@material-ui/icons/Search";
import InputBase from "@material-ui/core/InputBase";
import { ThemeProvider } from "@material-ui/styles";
import TheatersIcon from "@material-ui/icons/Theaters";
import FormControl from "@material-ui/core/FormControl";
import SearchButton from "./SearchButton";
import Button from "@material-ui/core/Button";

const useStyles = makeStyles(theme => ({
  title: {
    alignItems: "center",
    justifyContent: "start",
    //height: "100%",
    display: "flex",
    flexShrink: 0,
    width: "200px",
    padding: theme.spacing(1.5, 0, 1.5, 5),
    marginRight: theme.spacing(0)
    //pointerEvents: "none"
    // Remove for small screens
  },

  appBar: {
    padding: 10,
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(2),
    zIndex: theme.zIndex.drawer + 1,
    //boxShadow: 0,
    backgroundColor: "black"
  },
  button: {
    color: "white",
    fontSize: 16,
    fontWeight: 400,
    marginRight: 40,
    marginTop: 4,
    fontFamily: "Quicksand",
    borderRadius: 23
  },
  searchBar: {
    display: "flex",
    alignItems: "center",
    position: "relative",
    marginLeft: 0,
    marginRight: theme.spacing(8),
    //width: "200px",
    borderRadius: theme.shape.borderRadius + 15,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    "&:hover": {
      backgroundColor: fade(theme.palette.common.white, 0.25)
    }
  },
  searchIcon: {
    width: theme.spacing(7),
    color: "inherit",
    display: "flex",
    pointerEvents: "none",
    justifyContent: "center",
    alignItems: "center",
    position: "absolute" // I don't get this.
  },
  theatersIcon: {
    height: "auto",
    alignItems: "center",
    paddingTop: 1.4,
    marginRight: 10,
    color: "inherit",
    pointerEvents: "none",
    position: "relative" // I don't get this.
  },
  subToolbar: {
    backgroundColor: "black",
    zIndex: theme.zIndex.drawer
  }
}));

/*

*/

export default function MainBar({
  queryChange,
  searchSubmit,
  trendingShows,
  trendingFilms
}) {
  const classes = useStyles();
  return (
    <ThemeProvider>
      <AppBar style={{ margin: 0 }} position="fixed" className={classes.appBar}>
        <Toolbar variant="dense" disableGutters>
          {/* APP TITLE */}
          <div className={classes.title}>
            <Typography
              style={{
                fontSize: 35,
                fontFamily: "DM Serif Display",
                cursor: "pointer"
              }}
              onClick={() => {
                trendingShows();
              }}
              noWrap
            >
              Binge
            </Typography>
          </div>
          <div style={{ display: "flex", flexGrow: 1, marginTop: 5 }}>
            <Button
              className={classes.button}
              onClick={() => {
                trendingShows();
              }}
            >
              TV
            </Button>
            <Button
              className={classes.button}
              onClick={() => {
                trendingFilms();
              }}
            >
              MOVIES
            </Button>
          </div>
          <div>
            {/*
            <Typography
              style={{
                fontFamily: "Roboto",
                fontSize: 14,
                marginRight: 22,
                fontWeight: 100
              }}
              noWrap
            >
          
              Made by{" "}
              <a
                href="https://www.linkedin.com/in/tande-mungwa"
                target="_blank"
              >
                <font color="white">Tande Mungwa</font>
              </a>{" "}
              with React.js & TMDb
            </Typography>
            */}
          </div>

          {/* SEARCH BUTTON */}
          <div className={classes.searchBar}>
            {/* SEARCH ICON */}
            <div className={classes.searchIcon}>
              <SearchIcon />
            </div>
            {/* INPUT */}
            <SearchButton
              style={{ width: 2 }}
              queryChange={queryChange}
              searchSubmit={searchSubmit}
            />
          </div>
        </Toolbar>
      </AppBar>
    </ThemeProvider>
  );
}
